.header {
  font-family: 'Source Sans Pro', sans-serif;
  margin-top: 60px;
}

.headerTitles {
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #444;
}

.headerTitleSmall {
  position: absolute;
  top: 18%;
  font-size: 20px;
}

.headerTitleLarge {
  position: absolute;
  top: 20%;
  font-size: 100px;
}

.headerImg {
  width: 100%;
  height: 450px;
  margin-top: 60px;
  object-fit: cover;
}
